












































import Vue, { PropOptions } from 'vue'
export default Vue.extend({
  props: {
    label: {
      type: String,
      required: true,
    },
  },

  computed: {
    groupClass (): object {
      return {
        'open': this.open,
      }
    },
  },

  data () {
    return {
      open: true,
    }
  },

  methods: {
    toggleOpen () {
      this.open = !this.open
    },
  },
})
