















import Vue from 'vue'
import MetisMenu from 'metismenu/src'
// import MetisMenu from 'metismenu/dist/modules/index.js'
interface IMenuItem {
  href: string;
  label: string;
  icon: string;
}

export default Vue.extend({
  computed: {
    menuItems (): IMenuItem[] {
      return [
        {
          href: '/map',
          label: 'Map',
          icon: 'fa-map',
        },

        {
          href: '/',
          label: 'Dashboard',
          icon: 'fa-diamond',
        },

      ]
    },
  },
  mounted () {
    const menu = new MetisMenu(this.$el)

    menu.init()
  },
})
