



























import Vue from 'vue'
export default Vue.extend({
  props: {
    title: {
      type: String,
    },
    noPadding: {
      type: Boolean,
      default: false,
    },
    noBorder: {
      type: Boolean,
      default: false,
    },
    flex: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    panelClass (): any {
      return {
        'no-padding': this.noPadding,
        'no-border': this.noBorder,
        'flex': this.flex,
      }
    },
  },
})
